header{
    margin-top: -1px;
    background: {
        image: url(../img/hero/h1_hero.png);
        repeat: no-repeat;
        position: top center;
        size: cover;
        attachment: fixed;
    }
    position: relative;
    height: 100vh;
    .header-title{
        font-size: 55px;
        display: inline-block;
        position: relative;
        text-transform: uppercase;
        -webkit-text-stroke: 1px #f6f7f8;
        -webkit-text-fill-color: transparent;
        line-height: 1.2;
        @media(max-width: 767px) {
            font-size: 36px;
        }
    }
    h1{
        font-size: 110px;
        font-weight: 700;
        margin-bottom: 14px;
        color: #fff;
        line-height: 1.2;
        margin-bottom: 40px;
        text-transform: uppercase;
        @media(max-width: 767px) {
            font-size: 70px;
        }
    }
    .btn2 {
        float: left;
        &:hover span{
            color: #FFF !important;
        }
    }
}

.traning{
    .single-traning{
        position: relative;
        &:before{
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            z-index: 1;
            background-color: rgba(0,0,0,.5)
        }
        .traning-img{
            img{
                width: 100%;
            }
        }
        .traning-content{
            position: absolute;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
            color: #fff !important;
            z-index: 2;
            h3{
                font-size: 50px;
                font-weight: 500;
            }
            p{
                font-size: 16px;
                padding: 0 82px;
                line-height: 1.6;
                margin-bottom: 30px;
            }
        }
    }
}

.services{
    .services-single{
        margin-top: 50px;
        img{
            width: 100%;
        }
        .services-content{
            margin-top: 20px;
            .services-single-title{
                a{
                    color: #fff;
                    font-weight: 500;
                    letter-spacing: 1px;
                    transition: all 0.5s;
                    &:hover{
                        color: $mainColor;
                    }
                }
            }
            p{
                color: #fff;
                font-size: 16px;
                font-family: "Roboto Condensed", sans-serif;
            }
        }
    }
}

.gallery{
    .dh-container{
        position: relative;
        overflow: hidden;
        margin-bottom: 30px;
        .dh-overlay{
            background-image: -webkit-linear-gradient(170deg, rgba(255, 0, 0, 0.3) 0%, black 100%);
            width: 100%;
            height: 100%;
            text-align: center;
            line-height: 300px;
            color: #FFF;
            h3{
                position: absolute;
                bottom: 100px;
                left: 20px;
                font-weight: bold;
            }
            a{
                color: #fff;
                font-size: 20px;
                width: 0;
                height: 0;
                border-bottom: 100px solid red;
                border-left: 100px solid transparent;
                text-align: center;
                display: inline-block;
                position: absolute;
                bottom: 50px;
                right: 30px;
                i{
                    position: absolute;
                    top: 57px;
                    right: 22px;
                }
            }
        }
    }
}

.pricing{
    .pricing-single{
        margin-top: 50px;
        border: 1px solid rgba(255, 255, 255, 0.2);
        padding: 36px 24px 25px 24px;
        box-shadow: 0px 6px 6px 0px rgba(2, 25, 65, 0.08);
        .pricing-img{
            width: 80px;
            height: 80px;
            line-height: 80px;
            background: $mainColor;
            border-radius: 50px;
            margin-bottom: 31px;
            text-align: center;
            display: inline-block;
            box-shadow: 0px 15px 25px rgba(66, 85, 164, 0.08);
            i{
                font-size: 30px;
                color: #fff;
                line-height: 80px;
                transform: rotate(-45deg);
            }
        }
        .pricing-content{
            font-family: "Roboto Condensed", sans-serif;
            h5{
                color: #fff;
                font-size: 25px;
            }
            P{
                color: $mainColor;
                font-size: 30px;
                span{
                    font-size: 14px;
                    color: #fff;
                    margin-left: 5px;
                }
            }
            ul{
                margin-bottom: 30px;
                li{
                    margin-bottom: 12px;
                    span{
                        color: #fff;
                        img{
                            float: left;
                            margin-right: 15px;
                            margin-top: 6px;
                        }
                    }
                }
            }
        }
    }
}

.about-me{
    .about-me-img{
        img{
            width: 100%;
        }
    }
    .about-me-content{
        color: #fff;
        h2{
            font-size: 50px;
            margin-bottom: 20px;
        }
        P{
            width: 80%;
            font-family: "Roboto Condensed", sans-serif;
            line-height: 30px;
        }
        a{
            margin-top: 20px;
            float: left;
        }
    }
}

.blog{
    padding-bottom: 80px;
    .blog-single{
        margin-top: 30px;
        img{
            margin-bottom: 20px;
            width: 100%;
        }
        .blog-content{
            color: #fff;
            h3{
                margin-top: 20px;
            }
        }
    }
}

.video-area{
    background: {
        image: url(../img/gallery/video-bg.png);
        repeat: no-repeat;
        position: top center;
        size: cover;
    }
    position: relative;
    height: 650px;
    .video-icon{
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
        right: 0;
        text-align: center;
        margin: 0 auto;
        a{
            position: relative;
            font-size: 30px;
            background: #fff;
            color: $mainColor;
            width: 110px;
            height: 110px;
            display: inline-block;
            text-align: center;
            line-height: 110px;
            border-radius: 50%;
            &:before{
                content: '';
                display: inline-block;
                position: absolute;
                top: -2px;
                left: -2px;
                bottom: -2px;
                right: -2px;
                border-radius: inherit;
                border: 1px solid #FF0000;
                animation: btnIconRipple 2s cubic-bezier(0.23, 1, 0.32, 1) both infinite;
            }
            i{
                line-height: 110px;
            }
        }
    }
}

@keyframes btnIconRipple{
    0% {
        border-width: 4px;
        -webkit-transform: scale(1);
        transform: scale(1);
      }
      50% {
        border-width: 1px;
        -webkit-transform: scale(1.5);
        transform: scale(1.5);
      }
      80% {
        border-width: 1px;
        -webkit-transform: scale(1.9);
        transform: scale(1.9);
      }
      100% {
        opacity: 0;
      }
}

.contact{
    background-color: #111;
    padding-bottom: 80px;
    .contact-single{
        margin-bottom: 40px;
        .contact-icon{
            i{
                float: left;
                background-color: $mainColor;
                width: 60px;
                height: 60px;
                line-height: 60px;
                color: #fff;
                text-align: center;
                border-radius: 50%;
                font-size: 22px;
            }
        }
        .contact-content{
            font-family: "Roboto Condensed", sans-serif;
            margin-left: 80px;
            color: #fff;
            p{
                margin-bottom: 8px;
            }
        }
    }
}

footer{
    .footer-section-one{
        border-bottom: 1px solid #1f2024;
        padding-bottom: 50px;
        img{
            width: 12%;
        }
        ul{
            margin-top: 30px;
            a{
                color: #fff !important;
                li{
                    display: inline-block;
                    margin-right: 20px;
                }
            }
        }
        .social-link{
            a{
                li{
                    i{
                        background-color: $secondColor;
                        width: 40px;
                        height: 40px;
                        line-height: 40px;
                        color: $mainColor;
                        text-align: center;
                        border-radius: 50%;
                        font-size: 16px;
                        transition: all 0.2s ease-in;
                        &:hover{
                            background-color: $mainColor;
                            color: #fff;
                        }
                    }
                }
            }
        }
    }
    .footer-section-tow{
        p{
            color: #c4c4c4;
            margin-top: 40px;
            i{
                color: red;
                margin-left: 3px;
                margin-right: 7px;
            }
        }
    }
}
.scroll-top {
    i{
        color: #fff;
        position: fixed;
        bottom: 50px;
        right: 50px;
        background: $mainColor;
        width: 40px;
        height: 40px;
        line-height: 40px;
        text-align: center;
        border-radius: 50%;
    }
}