.btn{
    background: #ff1313;
    text-transform: uppercase;
    padding: 27px 44px;
    color: #fff;
    cursor: pointer;
    display: inline-block;
    font-size: 18px;
    font-weight: 600;
    border-radius: 0px;
    line-height: 1;
    border: 2px solid #fff;
    -moz-user-select: none;
    letter-spacing: 1px;
    line-height: 0;
    margin: 10px;
    cursor: pointer;
    transition: color 0.4s linear;
    position: relative;
    z-index: 1;
    overflow: hidden;
    margin: 0;
    &:before{
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 101%;
        height: 101%;
        background: $mainColor;
        z-index: 1;
        border-radius: 5px;
        transition: transform 0.5s;
        transition-timing-function: ease;
        transform-origin: 0 0;
        transition-timing-function: cubic-bezier(0.5, 1.6, 0.4, 0.7);
        transform: scaleX(0);
        border-radius: 0px;
    }
}
.btn:hover::before {
    transform: scaleX(1);
    color: #fff !important;
    z-index: -1;
}
.btn:hover {
    background-position: right;
    color: #fff;
}

.btn2{
    position: relative;
    border: 1px solid #fff;
    display: block;
    text-align: center;
    cursor: pointer;
    text-transform: uppercase;
    outline: none;
    overflow: hidden;
    color: #fff !important;
    font-size: 18px;
    letter-spacing: 1px;
    background-color: transparent;
    height: 50px;
    line-height: 50px;
    width: 190px;
    margin: 0 auto;
    box-shadow: 0 5px 15px rgba(0,0,0,0.20);
    text-decoration: none !important;
    span{
        position: relative; 
        z-index: 1;
    }
    &:after{
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        height: 490%;
        width: 140%;
        background: $mainColor;
        transition: all .5s ease-in-out;
        transform: translateX(-98%) translateY(-25%) rotate(45deg);
    }
    &:hover:after{
        transform: translateX(-9%) translateY(-25%) rotate(45deg);
        color: #fff !important;
    }
}