body{
    background-color: #000;
    font-family: 'Oswald', sans-serif;
    .p-t{
        padding-top: 70px;
    }
    .section-title{
            h2{
                font-size: 120px;
                -webkit-text-stroke: 1px #f6f7f8;
                -webkit-text-fill-color: transparent;
                @media (max-width: 767px) {
                    font-size: 70px;
                }
            }
    }
}

::selection {
    color: #000;
    background: $mainColor;
}