.navbar{
    margin-top: 20px;
    padding-bottom: 20px;
    padding-top: 20px;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    background: transparent !important;
    z-index: 9;
    min-height: 70px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    .navbar-collapse {
        @media (max-width: 991px) {
            background: #222 !important;
            margin-top: 30px;
        }
    }
    .navbar-nav{
        li{
            position: relative;
            margin-left: 35px;
            .nav-link{
                color: white !important;
                font-size: 14px;
                text-transform: capitalize;
                transition: all .3s ease;
                margin-bottom: 8px;
                &:hover:before{
                    width: 100%;
                }
            }
        }
        .active{
            &:before{
                content: "";
                position: absolute;
                left: 0px;
                bottom: 0;
                background: $mainColor;
                height: 2px;
                width: 100%;
                transition: all .3s ease;
            }
        }
    }
    .social-link{
        display: inline-block;
        font-size: 16px;
        margin-top: 8px;
        position: relative;
        a{
            color: #fff !important;
            i{
                margin-left: 20px;
            }
        }
    }
    .navbar-toggler {
        outline: none;
        border: none;
        .icon-bar{
            background-color: $mainColor;
            padding: 8px 14px;
            i{
                color: #fff;
            }
        }
    }
}

.navbar-bg{
    background: linear-gradient(to bottom, #950101 0%, #630102 50%, #3d0302 100%) !important;
    box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.05);
    padding: 0;
    position: fixed;
    top: -120px;
    left: 0;
    width: 100%;
    transform: translateY(100px);
    transition: transform .5s ease;
    min-height: 100px;
}